.page-header {
    padding: 10px 20px;
    background-color: #333;
    color: #fff;
}

.page-body {
    overflow-y: auto;
    height: 60vh;
    border: 1px solid #777;
    padding: 5px;
    box-shadow: rgba(100, 100, 100, .3) 0 0 10px inset;
    ol {
        margin-left: 20px;
    }
    code {
        background-color: rgba(100, 100, 100, .3);
        padding: 5px;
    }
}

.page-footer {
    text-align: right;
    padding: 5px;
    .btn {
        background-color: #333;
        color: #fff;
    }
}